div.cards-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    gap: 5px;
}

div.card-container {
    background-color: white;
    width: fit-content;
    border-style: solid;
    border-radius: 5px;
    border-color: white;
} 

div.card-container > *{
    width: min-content;
}
  
div.card-container .img-container {
    width: 100%;
}

div.card-container > p {
    white-space: nowrap; 
    width: 50px; 
    overflow: hidden;
    text-overflow: ellipsis; 
}

